import PusherCollector from "./pusher-collector";
import Pusher from "pusher-js";

class PusherClientCollector extends PusherCollector {
  constructor(channelName) {
    console.trace("hello");
    const pusher = new Pusher(process.env.PUSHER_KEY, {
      cluster: process.env.PUSHER_CLUSTER,
      authEndpoint: process.env.PUSHER_AUTH_ENDPOINT
    });

    super(pusher, `private-${channelName}`);
  }
  async initialize() {
    return new Promise((resolve, reject) => {
      const channel = this._pusher.subscribe(this._channelName);
      channel.bind("pusher:subscription_succeeded", () => {
        resolve();
      });
      channel.bind("pusher:subscription_error", e => {
        console.error(e);
        reject(e);
      });
    });
  }
}

export default PusherClientCollector;
